
#app {
  font-family: Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #32485F;
  font-size: 16px;
  max-width: 1024px;
  margin: 0 auto;
}

.configurator {
  margin-bottom: 15px;
}

.question {
  text-align: left;
  font-size: 14px;
}

.answers {
  margin-top: 10px;
}

.answer {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 5px 10px;
  font-family: Consolas,Monaco,'Andale Mono','Ubuntu Mono',monospace;
  min-width: 100%;
  max-width: 100%;
  min-height: 140px;
  text-align: left;
  font-size: 16px;
}

.paginator {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.paginator__item {
  margin-bottom: 0.5em;
  padding: 0.4em 1.8em;
  border-radius: 7px;
  background-color: lightblue;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  color: #32485F;
  border: 0.2em solid transparent;
  -webkit-transition: border 0.2s ease;
  transition: border 0.2s ease;
}
.paginator__item:hover {
    background-color: #82cde6;
    cursor: pointer;
}
.paginator__item_current {
    border: 0.2em solid #c3b9b4;
}
